import { Component,Input,Output,EventEmitter,OnChanges,SimpleChange } from '@angular/core';

import { Todo } from '../data-class/todo';

@Component({
    selector:'remain-archive',
    template:`
             <span>{{remaining}} of {{totalLen}} remaining</span>
             <button (click)="archive()">[archive]</button> 
    `
})
export class RemainArchiveComponent implements OnChanges {
    @Input() tdListIn:Todo[];
    @Output() delDone_evt=new EventEmitter<Todo[]>();
    
    remaining:number;
    totalLen:number;
    
    ngOnChanges(changes:{[propName:string]:SimpleChange}){   //SimpleChange need import
        let tds:Todo[]=changes['tdListIn'].currentValue;
        if (tds){
            this.totalLen=tds.length;
            this.remaining=tds.filter(td=>!td.done).length;
        }
    }
    
    archive():void{
         let oldtds:Todo[]=this.tdListIn;
         this.delDone_evt.emit(oldtds.filter(oldtd=>!oldtd.done));
    }
}